import React, { useEffect, useState } from 'react';
import firebase from '../Firestore/Firestore';
import "firebase/firestore";

import './Portfolio.css';

const Portfolio = () => {

    const [projects,setProjects] = useState([]);
    const [index,setIndex] = useState(0);

    useEffect(()=>{
        var db = firebase.firestore();
        db.collection("projects").get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
                setProjects(projects => [...projects, doc.data()]);
            });
        });
    },[])

    const navigatePortfolio = (direction) => {
        if (direction==='left') setIndex(index<=0?0:index-1);
        if (direction==='right') setIndex(index+1>=projects.length-1?projects.length-1:index+1);
    }

    const renderProjects = (projects) => {
        const e = projects[index];
        if(e){
            return (
                <div key={e.name} className="project">
                    <div className="project-image">
                        <a href={e.url} target="_blank" rel="noreferrer" title={e.name}>
                            <img src={e.image} alt={e.name}/>
                        </a>
                    </div>
                    <div className="project-info">
                        <a href={e.url} target="_blank" rel="noreferrer" title={e.name}>
                            <h2>{e.name}</h2>
                        </a>
                        <p>{e.description}</p>
                    </div>
                </div>
            )
        }else{
            return (
                <div className="project">
                    <div className="project-info">
                        <i className="spinner icon"></i>
                    </div>
                </div>
            )
        }
    }

    return (
        <div id="portfolio">
            <h1>Portfolio</h1>
            <p>Slide to the sides to see other projects.</p>
            <div className="projects">
                <div className="portfolio-nav left" onClick={()=>navigatePortfolio('left')}>
                    <i className="arrow alternate circle left icon"></i>
                </div>
                {renderProjects(projects)}
                <div className="portfolio-nav right" onClick={()=>navigatePortfolio('right')}>
                    <i className="arrow alternate circle right icon"></i>
                </div>
            </div>
            <div className="old-projects welcome">
                <p>There are also some old projects that are still online:</p>
                <ul>
                    <li>
                        <a href="https://cielpartners.com/" target="_blank" rel="noreferrer" title="Ciel Partners - Growth Partners in NYC">Ciel Partners - Growth Partners in NYC</a>
                    </li>
                    <li>
                        <a href="https://marfran.com.ve/" target="_blank" rel="noreferrer" title="MarFran - Importadora de Licores en Venezuela">MarFran - Importadora de Licores en Venezuela</a>
                    </li>
                    <li>
                        <a href="https://www.unonueveunosiete.com/" target="_blank" rel="noreferrer" title="UNONUEVEUNOSIETE - Marca de Joyas hechas en Venezuela">UNONUEVEUNOSIETE - Marca de Joyas hechas en Venezuela</a>
                    </li>
                </ul>
            </div>
        </div>
    )
}

export default Portfolio;